import React, {useEffect} from 'react';
import {connect} from "react-redux";
import {updateData} from '../../actions/actions'
import Pagination from '@material-ui/lab/Pagination';
import {TypographyStyled, Box} from './style'

function Main(props) {
    let limit = 10
    const handlePageChange = async (offset) => {
        props.updateData(limit, offset)
    }
    useEffect(() => {
        handlePageChange(0)
    }, [])

    const handlePagination = async (e) => {
        handlePageChange((Number(e.target.innerText)-1)*limit)
    }

    return (<>
        <div>
            
            {props.links && props.links.data && props.links.data.map((item, index) => {
                return <Box key={index}>
                        <TypographyStyled>{item.URL}</TypographyStyled>

                    </Box>
            })}
            <Pagination count={Math.ceil(props.links && props.links.status && props.links.status[0].TOTALQTD / limit)} color='secondary' onChange={handlePagination} />
        </div>
    </>);
}

const mapStateToProps = state => ({links: state.linksdata.linksdata});
const mapDispatchToProps = dispatch => ({
    updateData: (limit, offset) => dispatch(updateData(limit, offset))
});

export default connect(mapStateToProps, mapDispatchToProps)(Main);
