import React, {useEffect} from 'react';
import {connect} from "react-redux";
import {updateData, cleanTable, getPendingJobs} from '../../actions/actions'
import Button from '@material-ui/core/Button';
import {Wrapper, TypographyStyled} from './styled'

function Status(props) {

    const handleCleanTable = async () => {
        props.cleanTable()
        setTimeout(() => {
            props.updateData(10, 0)
        }, 2000);
    }

    return (<Wrapper>
        {props.links && props.links.status && (props.links.status[0].TOTALQTD > 0) && <>
            <TypographyStyled>
                Now we have {props.links && props.links.status && props.links.status[0].TOTALQTD} urls stored. For these jobs, we already done {props.links && props.links.status && props.links.status[0].FINISHED} and still {props.links && props.links.status && props.links.status[0].PENDING} remaining.
            </TypographyStyled>        
            <Button variant='outlined' onClick={() => {props.updateData(10, 0)}} >Refresh database</Button>
            <Button variant='outlined' onClick={() => {handleCleanTable()}} >Clear database</Button>
            </>
        }
        
        {props.links && props.links.status && (props.links.status[0].TOTALQTD == 0) &&
            <TypographyStyled>
                Now we have {props.links && props.links.status && props.links.status[0].TOTALQTD} urls to job.
            </TypographyStyled>        
        }

    </Wrapper>);
}

const mapStateToProps = state => ({links: state.linksdata.linksdata, refresh: state.needrefresh});
const mapDispatchToProps = dispatch => ({
    cleanTable: () => dispatch(cleanTable()),
    updateData: (limit, offset) => dispatch(updateData(limit, offset)),
});

export default connect(mapStateToProps, mapDispatchToProps)(Status);

